<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
      max-width="800"
  >
    <v-card :disabled="data.loading" :loading="data.loading" class="pass-form">

      <a-card-title>
        <span>بررسی درخواست</span>
      </a-card-title>

      <v-card-text class="pt-6 pb-0">

        <v-form ref="form" v-if="renderComponent" >
          <v-row>

            <v-col class="pb-0" v-if="item && item.designer && item.designer.username" cols="12" md="6" style="font-size: 18px" >
              طراح : <strong><span v-if="item.designer && item.designer.first_name && item.designer.last_name" >{{item.designer.first_name + ' ' +item.designer.last_name}}</span><span v-else >{{item.designer.username}}</span></strong>
            </v-col>
            <ProductData class="pt-0" :item="item.product" />
            <v-col class="py-0" cols="12" md="4">
              <v-text-field
                  outlined
                  dense
                  @keypress="isNumber($event)"
                  :rules="[$store.state.rules.required(point,'امتیاز')]"
                  label="امتیاز"
                  color="txtf"
                  v-model="point"
              >

              </v-text-field>
            </v-col>
<!--            <v-col cols="12" md="4" >

            </v-col>-->
            <v-col class="py-0" cols="12" md="8">
              <v-textarea
                  outlined
                  dense
                  rows="1"
                  :rules="[$store.state.rules.required(referee_note,'نوشته')]"
                  label="نوشته داور"
                  color="txtf"
                  v-model="referee_note"
              >

              </v-textarea>
            </v-col>
            <v-col class="py-0" cols="12">

              <v-checkbox
                  class="mt-0"
                  hide-details
                  color="info"
                  v-if="data.type=='tender'"
                  label="سفارش فوق را تایید می کنم"
                  v-model="confirmed"
              >

              </v-checkbox>
              <v-checkbox
                  v-else
                  class="mt-0"
                  hide-details
                  color="info"
                  label="سفارش فوق را تایید می کنم"
                  v-model="chosen"
              >

              </v-checkbox>
            </v-col>
          </v-row>
        </v-form>

      </v-card-text>

      <v-card-actions>

        <v-spacer></v-spacer>
        <v-btn
            height="36"
            dark
            v-if="$store.state.permissions && $store.state.permissions['vote-for-plan']"
            @click="changeProductDesignerReferee()"
            color="#222e3c"
        >
          ثبت نظر
        </v-btn>
      </v-card-actions>
      <template v-slot:progress >
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>


import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import {forceRerender} from "@/components/Misc/forceRerender";
import {isChars} from "@/components/Misc/isChars";
import ProductData from "@/components/Misc/ProductData";

export default {
  name: "SingleProductDesigner",
  props : ["cdata"],
  mixins : [forceRerender,isChars],
  data () {
    return {
      confirmed : false,
      chosen : false,
      data : this.cdata,
      point : null,
      referee_note : null,
      item : {}
    }
  },
  methods : {
    fetchSingleProductDesigner () {
      this.data.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/productdesigner/single-productdesigners/'+this.data.idSelected,{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.item = response.data.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(()=>{
        this.data.loading = false;
      })
    },
    changeProductDesignerReferee (){
        this.$refs.form.validate();
        if(this.$refs.form.validate()){
          this.data.loading = true;
          axios.patch(this.$store.state.host + '/projects/api/v1/productdesigner/change-productdesignerreferee/'+this.item.id , {
            confirmed : this.confirmed,
            chosen : this.chosen,
            point : this.point,
            referee_note : this.referee_note
          },{
            headers: {
              Accept: "application/json",
              Authorization: `Bearer ${this.$store.state.userId}`,
            },
          }).then(() => {
            this.notify('رای شما ثبت شد','info')
            this.data.dialog = false;
            this.$emit('updatedP')
          }).catch(error => {
            this.errorHandle(error.response)
          }).finally(() => {
            this.data.loading = false;
          })
        }
    },
  },
  watch : {
    'data.dialog'(val){
      if(val) {
        this.fetchSingleProductDesigner();
      } else {
        this.item = {}
        this.point = null;
        this.referee_note = null;
        this.chosen = false;
        this.confirmed = false;
        this.forceRerender();
      }
    }
  },

  components: {ProductData, GDLoading, ACardTitle}
}
</script>

