<template>
  <div>
  <v-card :disabled="loading">
    <BlockItem>

      <span v-if="$route.name==='AllRequestRefrees'"  slot="title">لیست همه درخواست ها</span>
      <span v-else  slot="title">طراحان ({{$route.params.title}})</span>
      <template slot="icon">mdi-reorder-horizontal</template>

      <v-form ref="form">
        <v-row class="my-3" >
          <v-col cols="=12">
            <v-data-table
                hide-default-footer
                :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
            >

              <template v-slot:[`item.designer`]="{item}">
                <a @click="toSeeProfile(item.designer.id)" style="font-size: 14px" ><span v-if="item.designer && item.designer.first_name && item.designer.last_name" >{{item.designer.first_name + ' ' +item.designer.last_name}}</span><span v-else >{{item.designer.username}}</span></a>
              </template>

              <template v-slot:[`item.price`]="{item}">
                <span style="font-size: 12px" >{{item.price | digit}} تومان</span>
              </template>

<!--              <template v-slot:[`item.done`]="{item}">
                <span style="font-size: 12px" v-if="item.done" >پایان یافته</span>
                <span style="font-size: 12px" v-else >در حال انجام</span>
              </template>-->

              <template v-slot:[`item.status`]="{item}">
                <span class="table-flag-enabled table-flag-enabled--yellow" v-if="item.chosen===null && item.confirmed===null" >بررسی نشده</span>
                <span class="table-flag-enabled table-flag-enabled--green" v-else-if="item.chosen===true || item.confirmed===true" >تایید شده</span>
                <span class="table-flag-enabled table-flag-enabled--red" v-else >رد شده</span>
              </template>

              <template v-slot:[`item.product.location`]="{item}">
                <span style="font-size: 13px" v-if="item && item.product && item.product.location && item.product.location && item.product.location.province && item.product.location.province.name" >{{item.product.location.province.name}}({{ item.product.location.name }})</span>
              </template>

              <template v-slot:[`item.payed`]="{item}">
                <span class="table-flag-enabled table-flag-enabled--green" v-if="item && item.product && item.product.product_type && item.product.product_type.value && item.payed && (item.product.product_type.value=='tender')" >پرداخت شده</span>
                <span class="table-flag-enabled table-flag-enabled--red" v-else-if="item && item.product && item.product.product_type && item.product.product_type.value && !item.payed && (item.product.product_type.value=='tender')">پرداخت نشده</span>
                <span class="table-flag-enabled table-flag-enabled--yellow" v-else >پس پرداخت</span>
              </template>

              <template v-slot:[`item.id`]="{item}">
                <div class="d-flex justify-center">
                  <a-sbtn @clickBtn="toDetail(item.id,item.product.product_type.value)" color="#17a2b8" icon="mdi-library" alt="نمایش درخواست" />
                </div>
              </template>

            </v-data-table>
          </v-col>
        </v-row>
      </v-form>

    </BlockItem>
  </v-card>
    <SingleProductDesigner @updatedP="fetchP()" :cdata="reqsData" ></SingleProductDesigner>
    <ProfileDialog :ddata="profileData" />
  </div>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import axios from "axios";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import SingleProductDesigner from "@/components/Dashboard/Refree/SingleProductDesigner";
import ProfileDialog from "@/components/Misc/ProfileDialog";
export default {
  name: "ProductDesigners",
  created() {
    this.fetchP();
  },
  components: {ProfileDialog, SingleProductDesigner, ASbtn, BlockItem},
  data () {
    return {
      profileData : {
        idSelected : null,
        dialog : false,
      },
      reqsData : {
        dialog: false,
        loading: false,
        idSelected: null,
        type : false,
      },
      loading : false,
      data : {
        data : []
      }
    }
  },
  methods : {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    fetchP () {
      if (this.$route.name === 'AllRequestRefrees'){
        this.fetchAllReqsRefrees();
      } else {
        this.fetchProductDesigners();
      }
    },
    toDetail(id,value) {
      this.reqsData.idSelected = id;
      this.reqsData.dialog = true;
      this.reqsData.type = value;
    },
    fetchAllReqsRefrees () {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/productdesigner/refereeproductdesigners',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.data = response.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    },
    fetchProductDesigners () {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/productdesigner/productdesigners/'+this.$route.params.id,{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.data = response.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    }
  },
  watch : {
    '$route' (){
      this.fetchP();
    }
  },
  computed : {
    headers() {
      return [
        {
          text: 'طراح', class: 'header-table-gray',
          align: 'start',
          sortable: false,
          value: 'designer',
        },
        {
          text: 'عنوان پروژه',
          align: 'center',
          class: 'header-table-gray',
          value: 'product.title',
        },

        {
          text: 'موقعیت', class: 'header-table-gray',
          align: 'center',
          value: 'product.location',
        },{
          text: 'نوع', class: 'header-table-gray',
          align: 'center',
          value: 'product.product_type.title',
        },{
          text: 'قیمت پیشنهادی', class: 'header-table-gray',
          align: 'center',
          value: 'price',
        },{
          text: 'وضعیت', class: 'header-table-gray',
          align: 'center',
          value: 'status',
        },{
          text: 'پرداخت', class: 'header-table-gray',
          align: 'center',
          value: 'payed',
        },{
          text: 'امتیاز', class: 'header-table-gray',
          align: 'center',
          value: 'point',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },


      ]
    },
  },
}


/*



/projects/api/v1/productdesigner/productdesigners/{id}

on Click

/projects/api/v1/productdesigner/single-productdesigners/{id}


 */
</script>

