<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
      max-width="760px"
  >
    <v-card elevation="0" :disabled="loading" :loading="loading" >
      <v-card-text>
        <v-row class="py-3" >
          <v-col cols="12" md="3" >
            <v-avatar
                :color="color"
                class="mb-2 mt-3 d-block mx-auto"
                size="128"
            >
              <img
                  v-if="item.avatar"
                  alt="Avatar"
                  :src="item.avatar"
              >
              <img
                  v-else-if="!item.avatar && !loading"
                  alt="Avatar"
                  style="object-fit: cover"
                  src="../../assets/image/empty-photo.png"
              >
              <v-skeleton-loader
                  v-else
                  width="100%"
                  type="card"
              ></v-skeleton-loader>
            </v-avatar>
            <p v-if="item.first_name && item.last_name" class="profile-card-title mb-0 pa-0 text-center">{{ item.first_name }} {{ item.last_name }}</p>
            <p v-else-if="item.username" class="profile-card-title mb-0 pa-0 text-center">{{ item.username }}</p>
            <p v-if="item.nick_name" class="mb-0 text-center" ><strong>({{ item.nick_name }})</strong></p>
            <p class="text-muted mb-2 text-center" v-if="item.role && item.role.title" >{{ item.role.title }}</p>

            <div class="d-flex justify-center align-center mb-3" >
              <v-tooltip
              bottom
              v-if="item.email"
              color="black"
              >
                <template v-slot:activator="{on,attrs}">
                  <a v-on="on" v-bind="attrs" class="mx-1"  :href="'mailto: '+item.email" target="_blank">
                    <v-icon :color="color" >mdi-email</v-icon> </a>
                </template>
                ایمیل
              </v-tooltip>

              <v-tooltip
                  bottom
                  v-if="item.website_link"
                  color="black"
              >
                <template v-slot:activator="{on,attrs}">
                  <a class="mx-1" v-on="on" v-bind="attrs"  :href="'https://'+item.website_link" target="_blank">
                    <v-icon :color="color" >mdi-web</v-icon> </a>
                </template>
                وب سایت
              </v-tooltip>

              <v-tooltip
                  bottom
                  v-if="item.linkedin_link"
                  color="black"
              >
                <template v-slot:activator="{on,attrs}">
                  <a class="mx-1" v-on="on" v-bind="attrs"  :href="'https://instagram.com/'+item.linkedin_link" target="_blank">
                    <v-icon :color="color" >mdi-instagram</v-icon> </a>
                </template>
                اینستاگرام
              </v-tooltip>


              <v-tooltip
                  bottom
                  v-if="item.tel_link"
                  color="black"
              >
                <template v-slot:activator="{on,attrs}">
                  <a class="mx-1" v-on="on" v-bind="attrs"  :href="'https://t.me/'+item.tel_link" target="_blank">
                    <v-icon :color="color" style="transform: rotate(-45deg)" >mdi-send-circle</v-icon> </a>
                </template>
                تلگرام
              </v-tooltip>


            </div>

            <p class="my-3" v-if="item.bio" >{{item.bio}}</p>
          </v-col>
          <v-col cols="12" md="9" class="py-3" >
            <v-tabs
                :slider-color="color"
                icons-and-text
                show-arrows
                v-model="tab"
                background-color="transparent"
                :color="color"
                grow
            >
              <v-tab
                  v-for="(item,i) in items"
                  :key="i+'pItmesg'"
              >
                {{ item.text }}
                <v-icon>{{ item.icon }}</v-icon>
              </v-tab>
            </v-tabs>
            <v-tabs-items v-model="tab">
              <v-tab-item
                  v-for="(item,i) in items"
                  :key="i+'titemsss'"
              >
                <v-card
                    flat
                    elevation="0"
                >
                  <div style="padding: 16px 4px">
                    <div v-if="i===0" >
                      <div class="profile-case-item" >
                        <p v-for="(item,index) in skills" :key="index+'sskills'" class="secondary--text" > <v-icon :color="color" small class="ml-1" >mdi-school-outline</v-icon> {{item.title}}</p>
                      </div>
                    </div>
                      <div v-if="i===1" >
                        <div class="profile-case-item" >
                          <p v-for="(item,index) in honors" :key="index+'hhonors'" class="secondary--text" > <v-icon :color="color" small class="ml-1" >mdi-star-outline</v-icon> {{item.title}}</p>
                        </div>
                      </div>
                      <div v-if="i===2" >
                        <div v-for="(item,index) in certificates.data" :key="index+'scertsifist'" class="certifi-group mb-6 mt-2">


                            <p class="my-3">
                              <v-icon small>mdi-certificate</v-icon>
                              گواهی <strong>{{ index+1 }} :</strong></p>

                          <v-row class="py-3">
                            <v-col class="py-1" v-if="item.title" cols="12" md="4">
                              <div class="certifi-item">
                                عنوان : <strong>{{ item.title }}</strong>
                              </div>
                            </v-col>
                            <v-col class="py-1" v-if="item.reference" cols="12" md="4">
                              <div class="certifi-item">
                                مرجع : <strong>{{ item.reference }}</strong>
                              </div>
                            </v-col>
                            <v-col class="py-1" v-if="item.state" cols="12" md="4">
                              <div class="certifi-item">
                                وضعیت : <strong>{{ item.state }}</strong>
                              </div>
                            </v-col>
                            <v-col class="py-1" v-if="item.grade" cols="12" md="4">
                              <div class="certifi-item">
                                سطح : <strong>{{ item.grade }}</strong>
                              </div>
                            </v-col>
                            <v-col class="py-1" v-if="item.lic_number" cols="12" md="4">
                              <div class="certifi-item">
                                شماره گواهی : <strong>{{ item.lic_number }}</strong>
                              </div>
                            </v-col>
                            <v-col class="py-1" v-if="item.lic_file" cols="12" md="4">
                              <div class="certifi-item">
                                فایل گواهی : <strong><a :href="$store.state.host + item.lic_file" target="_blank" class="txtf--text">{{
                                  item.lic_file.substring(item.lic_file.lastIndexOf('/') + 1)
                                }}</a></strong>
                              </div>
                            </v-col>
                          </v-row>
                        </div>


                        <v-pagination
                            v-if="certificates.total_objects"
                            :value="options.page"
                            v-model="options.page"
                            color="info"
                            class="mt-6"
                            :length="parseInt(certificates.total_pages)"
                        ></v-pagination>
                        <p class="text-center  mb-2 mt-2 info--text" v-if="certificates.total_objects" style="font-size: 14px">
                          {{ certificates.total_objects }} گواهی</p>


                      </div>
                  </div>
                </v-card>
              </v-tab-item>
            </v-tabs-items>
          </v-col>
        </v-row>
      </v-card-text>
      <template v-slot:progress>
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>
import {isChars} from "@/components/Misc/isChars";
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";

export default {
  name: "ProfileDialog",
  components: {GDLoading},
  props : {
    ddata : null,
    color : {
      default : 'info'
    }
  },
  mixins: [isChars],
  data() {
    return {
      certificates : [],
      options : {
        page : 1
      },
      skills : [],
      honors : [],
      tab: null,
      items: [
        {text:'مهارت ها',icon : 'mdi-school'},
        {text:'افتخارات',icon : 'mdi-medal'},
        {text:'گواهی ها',icon : 'mdi-certificate'},
      ],
      item: {
        avatar : null
      },
      data: this.ddata,
      loading : false,
    }
  },
  watch: {
    'options.page'() {
      this.fetchCertificates();
    },
    'data.dialog'(val) {
      if (!val) {
        this.item = {
          avatar : null
        };
        this.skills = [];
        this.honors = [];
        this.certificates = [];
      } else {
        this.fetchProfile();
        this.fetchSkillData();
        this.fetchHonorData();
        this.fetchCertificates();
      }
    },
  },
  methods: {
    fetchProfile() {
      this.loading = true;
      axios.get(this.$store.state.host + '/accounts/api/v1/another-user/'+this.data.idSelected, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.item = response.data.data;
        console.log(this.item);
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    },
    fetchSkillData(){
      this.loading= true
      axios.get(this.$store.state.host + '/accounts/api/v1/skills/another-user-skills/'+this.data.idSelected,{
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.skills = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false
      })
    },
    fetchHonorData(){
      this.loading= true
      axios.get(this.$store.state.host + '/accounts/api/v1/skills/another-user-honors/'+this.data.idSelected,{
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.honors = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false
      })
    },
    fetchCertificates() {
      this.loading = true
      axios.get(this.$store.state.host + '/accounts/api/v1/another-user-certificates/'+this.data.idSelected, {
        params : this.options,
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.certificates = response.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false
      })
    },
  }
}
</script>

